import * as React from 'react';
import { MenuItem } from '@ali/we-design';
import { PluginRoles } from '@ali/4ever-bamboo';
import { MenuItemInnerProps } from '@ali/4ever-component';
;
import { toggleUnderline } from '../actions';

export default function UnderlineMenu(props: MenuItemInnerProps) {
  const { menuItem, controller } = props;
  const { isCollapsedAtEdge } = controller.value.selection;
  const { title, ...rest } = menuItem;

  const isDisabled = isCollapsedAtEdge
    || controller.query('isSelectionInImage')
    || controller.query('isSelectionInHr')
    || controller.query('isSelectionInListSymbol');

  const isActive = controller.query('isUnderline');

  const handleClick = () => controller.run('onAction', toggleUnderline());

  return (
    <MenuItem {...rest} onClick={handleClick} disabled={isDisabled} selected={isActive} role={PluginRoles.underline}>
      {title}
    </MenuItem>
  );
}
